import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Container from '@material-ui/core/Container';
import IconButton from '@material-ui/core/IconButton';
import TwitterIcon from '@material-ui/icons/Twitter';
import Typography from '@material-ui/core/Typography';
import logo from "../images/logo_42b.png";
import { makeStyles } from '@material-ui/core/styles';
import Link from '@material-ui/core/Link';

const useStyles = makeStyles((theme) => ({
  appbar:{
    backgroundColor:"#fed229",
    opacity: 1.00,
    borderTop: 0,
    borderLeft: 0,
    borderRight:0,
  },
  divFlex:{
    flexGrow: 1,
  },
  link: {
    margin: theme.spacing(1, 1.5),
    flexGrow: 1,
  },
  logo:{
    padding: theme.spacing(2, 0,1),
    justifyContent: "center",
  }
}));

const Navbar = () => {
  const classes = useStyles();
  const preventDefault = (event) => event.preventDefault();
  return(
    <AppBar elevation={0} variant="outlined" position="sticky" className={classes.appbar}>
      <Container maxWidth="md">
      <Toolbar  display="flex" flexDirection="row">
        <Link href="https://godroll.co" className={classes.logo}>
        <img src={logo} alt="Godroll Logo" />
        </Link>
        <Typography variant="h6" component="div" className={classes.divFlex}>

        <Link variant="button" color="textPrimary" href="#" className={classes.link}>
          Charity
        </Link>
        <Link variant="button" color="textPrimary" href="#" className={classes.link}>
          Shapes
        </Link>
        <Link variant="button" color="textPrimary" href="#" className={classes.link}>
          Coins
        </Link>
        </Typography>


        <Link href="https://twitter.com/GodRollBros" >
         <IconButton
            size="large"
            aria-label="show more"
            color="primary"
          >
            <TwitterIcon />
          </IconButton>
          </Link>
      </Toolbar>
      </Container>
    </AppBar>
  );
}

export default Navbar;
