import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import CameraIcon from '@material-ui/icons/PhotoCamera';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Link from '@material-ui/core/Link';
import Chip from '@material-ui/core/Chip';
import ReactPlayer from 'react-player'

import vidShape from '../videos/3094.3748ff8d.mp4';



const useStyles = makeStyles((theme) => ({
  icon: {
    marginRight: theme.spacing(2),
  },
  heroContent: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(8, 0, 6),
  },
  heroButtons: {
    marginTop: theme.spacing(4),
  },
  cardGrid: {
    paddingTop: theme.spacing(8),
    paddingBottom: theme.spacing(8),
  },
  card: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
  },
  cardMedia: {
    paddingTop: '56.25%', // 16:9
  },
  cardContent: {
    flexGrow: 1,
  },
  footer: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(6),
  },
  chipWhite:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#ffffff',
    color:'#000000',
  },
  chipRed:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#EA3323',
    color:'#000000',
  },
  chipYellow:{
    margin: theme.spacing(0,1, 0,0),
    backgroundColor: '#FFBB25',
    color:'#000000',
  },
}));

class ResponsivePlayer extends React.Component {
  render () {
    return (
      <div className='player-wrapper'>
        <ReactPlayer
          className='react-player'
          url={vidShape}
          width='100%'
          height='100%'
          playing={true}
          loop='true'
          muted={true}
        />
      </div>
    )
  }
}

export default function Album() {
  const classes = useStyles();

  return (
    <Container maxWidth="sm" component="main" className={classes.heroContent}>


          <Grid container spacing={4}>

              <Grid item  xs={12} sm={6} md={6}>
                <Card className={classes.card}>

                  <ResponsivePlayer />
                  <CardContent className={classes.cardContent}>
                    <Typography gutterBottom variant="h5" component="h2">
                      Heading
                    </Typography>
                    <Typography>
                      This is a media card. You can use this section to describe the content.
                    </Typography>
                    <Chip size="small" variant="outlined" color="primary" className={classes.chipWhite} label="&nbsp;&nbsp;" />
                    <Chip size="small" variant="outlined" color="primary" className={classes.chipYellow} label="&nbsp;&nbsp;" />
                    <Chip size="small" variant="outlined" color="primary" className={classes.chipRed} label="&nbsp;&nbsp;" />
                  </CardContent>

                </Card>
              </Grid>

          </Grid>

</Container>

  );
}
